.boxsignup {
  width: 500px;
  padding: 45px;
  position: absolute;
  top: 10%;
  left: 10%;
  background: rgb(197, 240, 214);
  text-align: center;
  transition: 0.25s;
  margin-top: 0px;
  border-radius: 20px;
  border: 2px solid rgb(168, 160, 160);
}

.boxsignup:hover {
  box-shadow: 0px 30px 18px -8px rgba(0, 0, 0, 0.1);
  transform: scale(1.05, 1.05);
}

.box input[type="text"], .box input[type="password"] {
  border: 0;
  background: white;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 10px 10px;
  width: 250px;
  outline: none;
  color: black;
  border-radius: 24px;
  transition: 0.25s
}

.boxsignup input[type="text"], .boxsignup input[type="password"] {
  border: 0;
  background: white;
  display: block;
  margin: auto;
  text-align: center;
  border: 2px solid #3498db;
  padding: 10px 10px;
  width: 250px;
  outline: none;
  color: black;
  border-radius: 24px;
  transition: 0.25s
}

.box h1 {
  color: black;
  text-transform: uppercase;
  font-weight: 500;
  font-weight: bold;
}

.box input[type="text"]:focus, .box input[type="password"]:focus {
  width: 300px;
  border-color: #2ecc71
}

.box input[type="submit"] {
  border: 0;
  background: none;
  display: block;
  margin: 20px auto;
  text-align: center;
  border: 2px solid #2ecc71;
  padding: 14px 40px;
  outline: none;
  color: rgb(8, 8, 8);
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer
}

.box input[type="submit"]:hover {
  background: #2ecc71
}

.forgot {
